.slider__scale {
  box-sizing: border-box;
  position: absolute;
  width: calc(100% - 24px);
  left: 12px;
  top: 11px;
  height: 4px;
  border-radius: 4px;
  background: grey;
  z-index: 0;
}
.slider__active-range {
  display: block;
  height: 100%;
  width: 100%;
  background: #b3d4fc;
}
.slider__vertical-scale {
  box-sizing: border-box;
  position: absolute;
  height: calc(100% - 24px);
  left: 11px;
  top: 12px;
  width: 4px;
  border-radius: 4px;
  background: grey;
  z-index: 0;
}
.slider__vertical-active-range {
  display: block;
  height: 100%;
  width: 100%;
  background: #b3d4fc;
}
.slider__thumb {
  box-sizing: border-box;
  display: block;
  position: absolute;
  height: 24px;
  width: 24px;
  z-index: 2;

  background: #F0F0F0;
  border: 1px solid #A4A4A4;
  border-radius: 50%;
  cursor: pointer;
}
.slider__tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted #b3d4fc; /* If you want dots under the hoverable text */
}
.slider__tooltip-text {
  font-family: "Fire Sans", Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;

  width: 25px;
  background-color: #b3d4fc;
  opacity: 0.7;
  color: white;
  text-align: center;
  padding: 5px 0;
  border-radius: 5px;

  position: absolute;
  top: -50px;
  z-index: 1;
}
.slider__tooltip-text::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 30%;
  border-width: 5px;
  border-style: solid;
  border-color: #b3d4fc transparent transparent transparent;
}
.slider__vertical-tooltip-text {
  font-family: "Fire Sans", Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;

  width: 25px;
  height: 18px;
  background-color: #b3d4fc;
  opacity: 0.7;
  color: white;
  text-align: center;
  padding: 3px 5px;
  border-radius: 5px;

  position: absolute;
  top: -15px;
  left: 30px;
  z-index: 1;
}

.slider__vertical-tooltip-text::after {
  content: " ";
  position: absolute;
  top: 30%;
  left: -30%;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #b3d4fc transparent transparent;
}
.slider__tooltip-hide {
  display: none;
}
